@import "fonts.scss";
@import "common.scss";

.main-screen__page {
   background-image: url('../img/bg.jpg');
   background-repeat: no-repeat;
   height: 810px;
}
.main-screen__container {
   position: relative;
   padding-top: 230px;
}
.main-screen__body {
   padding-left: 145px;
   overflow: hidden;
}
.main-screen__content {
   padding-top: 78px;
   display: flex;
   flex-direction: column;
   align-items: center;
}
.main-screen__content-stock {
   margin: 0 0 10px auto;
}
.main-screen__content-subtitle {
   position: relative;
   margin: 25px 0 15px 0;
   font-size: 27px;
   font-family: "futuraBook";
   color: rgb(255, 255, 255);
   line-height: 1.119;
   -moz-transform: matrix( 0.61417915801373,0,0,0.62366758213658,0,0);
   -webkit-transform: matrix( 0.61417915801373,0,0,0.62366758213658,0,0);
   -ms-transform: matrix( 0.61417915801373,0,0,0.62366758213658,0,0);
   &::before {
      content: "";
      background-color: rgb(255, 255, 255);
      opacity: 0.302;
      position: absolute;
      left: -270px;
      top: 50%;
      width: 255px;
      height: 2px;
      z-index: 14;
   }
   &::after {
      content: "";
      background-color: rgb(255, 255, 255);
      opacity: 0.302;
      position: absolute;
      left: 120px;
      top: 50%;
      width: 255px;
      height: 2px;
      z-index: 14;
   }
}
.main-screen__content-button {
   display: flex;
   gap: 19px;
}
.main-screen__content-btn {
   width: 190px;
   height: 56px;
   border-style: solid;
   border-width: 2px;
   border-color: rgb(255, 255, 255, 0.102);
   border-radius: 6px;
   background-color: rgba(247, 247, 247, 0);
}
.main-screen__content-btn-img {
   width: 100%;
   height: 100%;
}
.main-screen__button {
   border-radius: 10px;
   background-color: rgb(41, 211, 209);
   display: flex;
   align-items: center;
   gap: 40px;
   padding: 15px 35px;
   position: absolute;
   bottom: -30px;
}
.main-screen-span {
   font-size: 22.448px;
   font-family: "futuraBook";
   color: rgb(255, 255, 255);
   text-transform: uppercase;
   line-height: 1.336;
   -moz-transform: matrix( 0.61417915801373,0,0,0.62366758213658,0,0);
   -webkit-transform: matrix( 0.61417915801373,0,0,0.62366758213658,0,0);
   -ms-transform: matrix( 0.61417915801373,0,0,0.62366758213658,0,0);
}
.main-screen-arrow {
   width: 38px;
   height: 18px;
}

 